export default {
    state: {
		isEditing: true,
		isPrinting: false,
	},
	getters: {
		getIsEditing(state) {
			return state.isEditing
		},
		getIsPrinting(state) {
			return state.isPrinting
		}
	},
	mutations: {
		setIsEditing(state, payload) {
			state.isEditing = payload
		},
		setIsPrinting(state, payload) {
			state.isPrinting = payload
		}
	},
	actions: {
		setIsEditing({ commit }, payload) {
			commit('setIsEditing', payload)
		},
		setIsPrinting({ commit }, payload) {
			commit('setIsPrinting', payload)
		}
	}
}