<template>
    <v-container class="fill-height d-print-block" fluid>
        <v-row align="center" justify="center" class="d-print-block">
            <v-col cols="12" xs="12" sm="12" md="12">
                <v-toolbar elevation="1" class="mb-1" :tile="false">
                    <p class="title text-primary mt-4">Dados da Simulação</p>
                    <v-spacer />
                    <v-tooltip left>
                        <template v-slot:activator="{ on }">
                            <v-btn
                                v-on="on"
                                class="d-print-none"
                                icon
                                @click="setIsEditing(true)"
                            >
                                <v-icon>mdi-lead-pencil</v-icon>
                            </v-btn>                            
                        </template>
                        <span>Editar dados</span>
                    </v-tooltip>
                    <v-tooltip bottom>
                        <template v-slot:activator="{ on }">                            
                            <v-btn
                                v-on="on"
                                class="d-print-none"
                                icon
                                @click="setIsPrinting(false)"
                            >
                                <v-icon>mdi-arrow-left-bold-circle</v-icon>
                            </v-btn>
                        </template>
                        <span>Retornar para simulação</span>
                    </v-tooltip>
                </v-toolbar> 

                <Resultado />
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import Resultado from './Resultado'

export default {
    name: "Impressao",
    components: {
        Resultado,
    },
    data: () => ({
        headers: [
            {
                text: 'DATA',
                align: 'start',
                sortable: false,
                value: 'data'
            },
            { 
                text: 'JUROS',
                sortable: false,
                value: 'juros'
            },
            {
                text: 'CAPITAL',
                sortable: false,
                value: 'principal'
            },
            {
                text: 'PRESTAÇÃO',
                sortable: false,
                value: 'totalPagar'
            },
            {
                text: 'SALDO DEVEDOR',
                sortable: false,
                value: 'devedor'
            },            
        ]
    }),
    methods: {
        ...mapActions(["setIsEditing", "setIsPrinting"])
    },
    computed: {
        ...mapGetters({
            resultado: "getResultado",
            iofDiario: "getIOFDiario",
            iofAdicional: "getIOFAdicional",
            iofTotal: "getIOFTotal",
            tarifasTotais: "getTarifasTotais",
            jurosTotais: "getJurosTotais",
            dataLiberacao: "getDataLiberacaoFormatted",
            custoTotal: "getCustoTotal",
            vencimentoFinal: "getVencimentoFinal",
            prestacoes: "getPrestacoes",
            prazoTotalDias: "getPrazoTotalDias"
        })
    },
    filters: {
        currency: value =>
            value.toLocaleString("pt-BR", {
                style: "currency",
                currency: "BRL",
                useGrouping: true,
                minimumFractionDigits: 2
            }),
        decimal: value =>
            value.toLocaleString("pt-BR", {
                style: "decimal",
                useGrouping: true,
                minimumFractionDigits: 2,
                maximumFractionDigits: 2
            }),
        dataBase: value =>
            value.toLocaleString("pt-BR", {
                minimumIntegerDigits: 2
            }),
        percent: value =>
            value.toLocaleString("pt-BR", {
                style: "percent",
                minimumFractionDigits: 2
            })
    }
};
</script>

