import { paramsLinha, toFixed2Dec, toFloor2Dec, daysBetween2Dates } from '@/params'
import moment from 'moment'

export default {
    state: {
        resultado: {
            valor: 0,
            tac: 0,
            flat: 0,
            diaBase: 0,
            taxa: 0,
            dataLiberacao: '',
            qtdeParcelas: 0,
            carencia: 0,                   
        },
        iofDiario: 0,
        prestacoes: []
    },
    getters: {
        getResultado(state) {
            return state.resultado
        },
        getIOFDiario(state) {
            return toFloor2Dec(state.iofDiario)
        },
        getPrestacoes(state) {
            const prestacoes = []
            let prestacao = {}
            state.prestacoes.forEach(value => {
                prestacao = {}
                prestacao.data = moment(value.data).format('DD/MM/YYYY')
                prestacao.juros = toFixed2Dec(value.juros)
                prestacao.principal = toFixed2Dec(value.principal)
                prestacao.totalPagar = toFixed2Dec(value.principal) + toFixed2Dec(value.juros)
                prestacao.devedor = value.devedor
                prestacoes.push(prestacao)
            })
            return prestacoes
        },
        getIOFAdicional(state) {
            return state.resultado.valor * paramsLinha.aliquotaIOFAdicional
        },
        getIOFTotal(state, getters) {
            return getters.getIOFDiario + getters.getIOFAdicional
        },
        getTarifasTotais(state) {
            return state.resultado.tac + state.resultado.flat
        },
        getCustoTotal(state, getters) {
            return getters.getIOFTotal + getters.getTarifasTotais
        },
        getJurosTotais(state, getters) {
            const prestacoes = getters.getPrestacoes
            return prestacoes.map(prestacao => prestacao.juros).reduce((acc, val) => acc + val)
        },
        getParcelaCapital(state) {
            return state.resultado.valor / state.resultado.qtdeParcelas || 0
        },
        getDataLiberacaoFormatted(state) {
            return state.resultado.dataLiberacao
                ? moment(state.resultado.dataLiberacao).format('DD/MM/YYYY')
                : ''
        },        
        getTaxaAno(state) { 
            const taxa = state.resultado.taxa
            return (1 + taxa) ** (12) - 1
        },
        // prazo total em meses, desconsiderando quando há prestação inferior a 30 dias na primeira
        getPrazoTotalMeses(state) {
            return state.resultado.qtdeParcelas + state.resultado.carencia 
        }, 
        getVencimentoFinal(state) {
            const vencimento = state.prestacoes[state.prestacoes.length - 1].data
            return vencimento
                ? moment(vencimento).format('DD/MM/YYYY')
                : ''
        },
        getPrazoTotalDias(state) {
            const vencimento = state.prestacoes[state.prestacoes.length - 1].data
            return daysBetween2Dates(vencimento, state.resultado.dataLiberacao)
        }
    },
    mutations: {
        setResultado(state, payload) {
            state.resultado = payload            
        },
        setPrestacoes(state, payload) {
            state.prestacoes = payload
        },
        setIOFDiario(state, payload) {
            state.iofDiario = payload
        }
    },
    actions: {
        setPrestacoes({ commit, getters }, payload) {
            const prestacoes = []
            let periodo = 0 // intervalo entre os pagamentos
            let taxaPeriodo = 0 // guarda a taxa aplicada ao período considerado
            let prazoIOF = 0 // prazo em dias da prestação atual a partir da data de liberação para calculo do IOF Diário
            let iofDiario = 0
            let prazoTotal = 0 // controla as iterações que geram as parcelas
            let { valor, diaBase, dataLiberacao: dataReferencia, carencia } = payload

            // só gera as prestações se o valor estiver setado e diferente de 0
            if (valor === 0)
                return []

            // prestação 0 (início do crédito)
            const prestacao = {
                data: dataReferencia,
                juros: 0, // em real
                principal: 0, // parcela de capital
                devedor: valor
            }
            prestacoes.push({ ...prestacao }) // salva a prestação 0

            prazoTotal = getters.getPrazoTotalMeses

            // i de 1 até prazo total-1
            for (let i = 1; i < prazoTotal; i++) {
                if (moment(dataReferencia).date() >= diaBase) {
                    prestacao.data = moment(dataReferencia).add(1, "M").date(diaBase)
                } else {
                    prestacao.data = moment(dataReferencia).date(diaBase)
                }

                periodo = daysBetween2Dates(prestacao.data, dataReferencia)

                // converte a taxa ao ano em taxa do período (taxa proporcional)
                taxaPeriodo = (1 + getters.getTaxaAno) ** (periodo / 360) - 1
                // calcula a prestação de juros do período
                prestacao.juros = +(taxaPeriodo * prestacao.devedor).toFixed(3)

                // cálculo IOF Diário (limitado a 365 dias)
                prazoIOF += periodo
                if (prazoIOF <= 365) {
                    iofDiario += paramsLinha.aliquotaIOFDiario * prestacao.devedor * periodo
                } else {                    
                    if ((prazoIOF - periodo) <= 365) {
                        iofDiario += paramsLinha.aliquotaIOFDiario * prestacao.devedor * (365 - (prazoIOF - periodo))
                    }
                }

                // acrescenta a carência de quando a primeira reposição cair antes de D+30 e recalcula o prazo total
                if (periodo < 30 && i === 1){
                    carencia++
                    prazoTotal++
                }                            
                
                // se não está mais na carência, acrescenta o capital
                if (i > carencia) {
                    // aqui arredonda para baixo o capital, mas na [última não]
                    prestacao.principal = toFloor2Dec(getters.getParcelaCapital) 
                }

                // atualiza o devedor, arredondando para 2 decimais
                prestacao.devedor = toFixed2Dec(prestacao.devedor - toFloor2Dec(prestacao.principal))

                prestacoes.push({ ...prestacao })

                dataReferencia = prestacao.data // atualiza a data de referência para a próxima parcela              
            }            

            // última prestação
            if (moment(dataReferencia).date() >= diaBase) {
                prestacao.data = moment(dataReferencia).add(1, "M").date(diaBase)
            } else {
                prestacao.data = moment(dataReferencia).date(diaBase)
            }

            periodo = daysBetween2Dates(prestacao.data, dataReferencia)

            taxaPeriodo = (1 + getters.getTaxaAno) ** (periodo / 360) - 1

            prestacao.juros = +(taxaPeriodo * prestacao.devedor).toFixed(3)
            prestacao.juros = +prestacao.juros.toFixed(2)

            prestacao.principal = toFixed2Dec(prestacao.devedor) // arredondamento normal

            prestacao.devedor = 0

            prestacoes.push({ ...prestacao })
            // finaliza a última
            
            // atualiza o IOFDiário (até 365 dias)
            commit('setIOFDiario', iofDiario)

            // return prestacoes 
            commit('setPrestacoes', prestacoes)
        }
    }
}