import { objEmprestimo, strToFloat, paramsLinha } from '@/params'

export default {
    state: {
        emprestimo: { ...objEmprestimo },
    },
    getters: {
        getEmprestimo(state) {
            return state.emprestimo
        }
    },
    mutations: {
        setEmprestimo(state, payload) {
            state.emprestimo = { ...payload }
        },
        resetEmprestimo(state) {
            state.emprestimo = { ...objEmprestimo }
        }
    },
    actions: {
        setEmprestimo({ commit, dispatch }, payload) {
            commit('setEmprestimo', payload)    // atualiza o state
            dispatch('setResultado', payload)   // atualiza o módulo simulação
        },
        resetEmprestimo({ commit }) {
            commit('resetEmprestimo')
        },
        // Esta action "comita" a mutation setResultado que está no módulo simulação
        // Dados extraídos via destructuring do payload
        setResultado({ commit, dispatch }, { valor, tac, tacPercent, flat, flatPercent, diaBase, taxa, dataLiberacao, qtdeParcelas, carencia }) {            
            
            valor = strToFloat(valor)

            tac = strToFloat(tac)
            if (tacPercent) {
                tac *= valor / 100
            }
            tac = tac > paramsLinha.tacMax ? paramsLinha.tacMax : tac

            flat = strToFloat(flat)
            if (flatPercent) {
                flat *= valor / 100
            }
            if (flat !== 0) {
                flat = flat < paramsLinha.flatMin ? paramsLinha.flatMin : flat
            }
            flat = flat / valor * 100 > paramsLinha.flatPercentMax ? valor * paramsLinha.flatPercentMax / 100 : flat

            taxa = strToFloat(taxa) / 100

            const resultado = {
                valor,
                tac,
                flat,
                diaBase,
                taxa,
                dataLiberacao,
                qtdeParcelas,
                carencia
            }            

            commit('setResultado', resultado)
            dispatch('setPrestacoes', resultado)            
        }
    }
}