import moment from 'moment'

// Objeto base inicial do empréstimo
const objEmprestimo = {
    valor: '0',         // valor é tratado como string por conta da máscara
    tac: '0',           // tac, flat e taxa são tratadas como string por conta da máscara de percentual
    tacPercent: true,   // controla se a tac é percentual ou em real
    flat: '0',
    flatPercent: true,  // controla se a flat é percentual ou em real
    diaBase: 1,
    taxa: '0',
    dataLiberacao: new Date().toISOString().substring(0, 10), // yyyy-mm-dd
    qtdeParcelas: 1,
    carencia: 0
}

// Parâmetros da linha SAC simulada
const paramsLinha = {
    tacMax: 5000,   // valor máximo admitido para a TAC
    flatMin: 100,   // valor mínimo admitido para a Flat
    flatPercentMax: 5, // valor máximo admitido para a Flat
    aliquotaIOFAdicional: 0.0038,   // 0.38% de IOF Adicional
    aliquotaIOFDiario: 0.000041,    // 0.0041% ao dia (limitado a 365 dias)
}

// Converte string de percentual pt-br (com , como separador) em float
// Método genérico: não deve estar dentro da instância do Componente Vue,
// pois pode ser usado antes do componente ser criado
const strToFloat = (value) => {
    let aux = value

    if (typeof value === 'string') {
        aux = parseFloat(value.replace('%', '').replace(/\./g, '').replace(',', '.'))
    } else if (typeof value !== 'number') {
        aux = NaN
    }

    return aux
}

// Converte pra 2 decimais, truncando e arredondando quando necessário
// Corrige problemas do toFixed como arredondar 1.005 para 1.01, mas o arredondamento é truncado
const toFixed2Dec = (num) => Math.round((num + Number.EPSILON) * 100) / 100

// Trunca em 2 decimais (float)
const toFloor2Dec = (num) => Math.floor(num * 100) / 100

const daysBetween2Dates = (data1, data2) => moment(data1).diff(moment(data2), 'days')

export {
    objEmprestimo,
    paramsLinha,
    strToFloat,
    toFixed2Dec,
    toFloor2Dec,
    daysBetween2Dates,
}